<template id="home">
  <div class="bed-all">
    <img src="../assets/img/bg.png" alt="" class='bed-bg'>
    <Topbar class="bed">
      <img src="../assets/img/home/cwgl.png" alt="">
      <span>床位管理</span>
    </Topbar>
    <div class="office clearfix">
      <div class="bed-choose">
        <span>科室选择：</span>
        <a href="javascript:void(0)" @click="active(0)" :class="activeS==0?'active':''">全部</a>
        <a href="javascript:void(0)" v-for='(item,index) in lookList' :class="activeS==item.departmentId?'active':''" @click="active(item.departmentId)" data-departmentId="item.departmentId">{{item.name}}</a>
      </div>
      <div class="office-item" v-for="(item,index) in departmentList">
        <h3>{{item.name}}</h3>
        <div class="bed-main">
          <div class="item" v-for="(it,index) in item.rooms">
            <div class="item-top">{{it.name}}</div>
            <div class="item-bottom">
              <span v-for="(list,index) in it.beds">{{list.name}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import Vue from "vue";
import {Message} from 'iview'
var that= {};
import Topbar from '../components/topbar'
import storage from 'good-storage'
export default {
  data(){
   return{
     departmentList:[],
     activeS:'0',
     lookList:[],
   }
  },
  computed:{

  },
  beforeCreate(){
    that = this;
  },
  created(){

  },
  mounted:function(){
    that.getRem(1920, 100);
    that.searchDepartment();
  },
  methods:{
    /*首次查询所能看的科室*/
    searchDepartment(id){
      that.$get(
        that.global.url+'/hospital/department',
          {
            departmentId:id,
          }
      ).then((response) => {
        if(response.code==200){
          that.lookList = response.data;
          that.departmentList =  that.lookList;
        }else if(response.code==300){
          that.error(response.message)
        }else{}
      })
    },
    /*点击查询科室床位列表*/
    clickDepartment(id){
      that.$get(
        that.global.url+'/hospital/department',
          {
            departmentId:id,
          }
      ).then((response) => {
        if(response.code==200){
          that.departmentList = response.data;
          console.log(that.departmentList)
        }else if(response.code==300){
          that.error(response.message)
        }else{}
      })
    },
    active(id){
      that.activeS = id;
      if(id==0){/*全部*/
        that.clickDepartment();
      }else{
        that.clickDepartment(id);
      }
    }
  },
  components:{
    "Topbar":Topbar
  }
}

</script>
<style src="../assets/css/common.scss" lang="scss"></style>
<style src="../assets/css/bed.scss" lang="scss"></style>



