<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {

  data () {
    return {
      name: 'App',
      screenWidth: document.body.clientWidth
    }
  },
  watch:{
    screenWidth(val){
      if(!this.timer){
          this.screenWidth = val;
          this.timer = true;
         let that = this;
         setTimeout(function(){
             that.timer = false
         },400)
         this.getRem(1920, 100)
        }
    }
  },
  mounted:function(){
    const that = this;
    window.screenWidth = document.body.clientWidth;
    that.screenWidth = window.screenWidth;
    /*监听浏览器窗口变化*/
      window.onresize = () => {
          return (() => {
              window.screenWidth = document.body.clientWidth;
              that.screenWidth = window.screenWidth;
          })()
      }
  },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width:100%;
  height:100%;
}
</style>



