import Vue from 'vue'
import Router from 'vue-router'
import storage from 'good-storage'
import home from '../views/home.vue'
import login from '../views/login.vue'
import monitoring from '../views/monitoring.vue'
import bedManage from '../views/bedManage.vue'
import deviceManage from '../views/deviceManage.vue'
import accountManage from '../views/accountManage.vue'
import history from '../views/history.vue'
import routerManage from '../views/routerManage.vue'


Vue.use(Router)

const router =  new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'login',
      component:login ,
    },
    {
      path: '/home',
      name: 'home',
      component: home,
    },
    {
      path: '/monitoring',
      name: 'monitoring',
      component: monitoring,
    },
    {
      path: '/bedManage',
      name: 'bedManage',
      component: bedManage,
    },
    {
      path: '/deviceManage',
      name: 'deviceManage',
      component: deviceManage,
    },
    {
      path: '/accountManage',
      name: 'accountManage',
      component: accountManage,
    },
    {
      path: '/history',
      name: 'history',
      component: history,
    },
    {
      path: '/routerManage',
      name: 'routerManage',
      component: routerManage,
    }
  ]
})


/* 全局路由守卫 */
router.beforeEach((to, from, next) => {

  const nextRoute = ['accountManage', 'bedManage', 'deviceManage','routerManage','home','history','monitoring'];
  const nextRoute1 = ['accountManage', 'bedManage', 'deviceManage','routerManage'];
  let canGo;
  /*判断账号是否有权限*/
  console.log(storage.get("userInfo"))
  console.log(to.path)
  console.log(Boolean(storage.get("userInfo")))
  if(Boolean(storage.get("userInfo"))){/*已登录*/
    if(to.path === '/login'){/*登陆后跳转登录页-->结果跳转home*/
      next({path:'/home'})
    }else if(to.path === '/'){/*登陆后跳转登录页-->结果跳转home*/
      next({path:'/home'})
    }else{
      if(storage.get("userInfo").role==2){/*登陆后账号角色为科室管理员*/
        if (nextRoute1.indexOf(to.name) >= 0) {/*登陆后账号角色为科室管理员,如果要跳转这三个页面-->结果跳转home*/
            next({path:'/home'})
        }else{
          next()
        }
      }else{
        next()
      }
    }
  }else{/*未登录*/
    if (nextRoute.indexOf(to.name) >= 0){/*未登录且跳转到这六个页面-->结果跳转login*/
      next({path:'/'})
    }else{
      next()
    }
  }
});
export default router;



