<template>
  <div class="bar-box">
    <div class="returnHome" @click="backHome()">
      <img src="../assets/img/bed/out.png" alt="">
      <span>返回首页</span>
    </div>
    <div class="bar-title">
      <slot></slot>
    </div>

    <div class="user">
      <img src="../assets/img/tx.jpg" alt="">
      <span>{{userInfo.account}}</span>
    </div>
  </div>
</template>

<script>
import storage from 'good-storage'
var that= {};
export default {
  name: 'topbar',

  data () {
    return {
      userInfo:storage.get('userInfo')
    }
  },
  props: {

   },
   beforeCreate(){
        that = this;
    },
  methods:{
    hideModal:function(){
      this.ishideModal = true
    },
    /*回首页*/
    backHome(){
      this.$router.push({path: '/home'});
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.bar-box{
  padding:0 1.6rem;
  height:0.8rem;
  overflow:hidden;
  position:fixed;
  top:0;
  width:100%;
  z-index:1000;
  .returnHome{
    background: #1890FF;
    cursor:pointer;
    border-radius: 0.19rem;
    width:1.18rem;
    height:0.38rem;
    margin-top:0.21rem;
    float:left;
    img{
      width:0.16rem;
      height:0.16rem;
      float:left;
      margin:0.11rem 0 0 0.18rem;
    }
    span{
      font-family: PingFangSC-Regular;
      font-size: 0.14rem;
      color: #FFFFFF;
      margin-left:0.1rem;
      float: left;
      display:block;
      line-height:0.38rem;
    }
  }
  .bar-title{
    // width:2.3rem;
    position:absolute;
    left:50%;
    transform:translate(-50%,0);
    img{
      width:0.4rem;
      height:0.4rem;
      margin-top:0.2rem;
      float:left;
      margin-right:0.2rem;
    }
    span{
      font-family: PingFangSC-Medium;
      font-size: 0.42rem;
      color: #FFFFFF;
      display:block;
      line-height:0.8rem;
      float:left;
    }
  }
  .user{
    float:right;
    img{
      width:0.24rem;
      height:0.24rem;
      margin-top:0.28rem;
      border-radius:50%;
      float:left;
      margin-right:0.15rem;
    }
    span{
      display:block;
      float:left;
      font-family: PingFangSC-Regular;
      font-size: 0.14rem;
      color: #FFFFFF;
      letter-spacing: 0;
      line-height:0.8rem;
    }
  }
}
</style>



