<template id="home">
  <div class="all home-all">
    <img src="../assets/img/bg.png" alt="" class='home-bg'>
    <div class="topbar clearfix">
      <h1>体温实时智能监测平台</h1>
      <div class="user">
        <img src="../assets/img/tx.jpg" alt="" class="tx">
        <span>{{userInfo.account}}</span>
        <div class="out" @click="out">
          <img src="../assets/img/out.png" alt="" class="out-icon">
          <em>退出</em>
        </div>
      </div>
    </div>
    <div class="home-main clearfix">
      <div class="home-main-it">
        <div class="menu-item menu-it" @click="navToMon(0)">
          <div class="menu-item-cover" v-if="access0==false"></div>
          <div class="menu-item-top jkpt-item-top">
            <div class="item-top-center">
              <img src="../assets/img/home/jcpt.png" alt="">
            </div>
          </div>
          <div class="menu-item-bottom">监测平台</div>
        </div>
        <div class="menu-item menu-it" @click="navToMon(1)">
          <div class="menu-item-cover" v-if="access1==false"></div>
          <div class="menu-item-top lsjl-item-top">
            <div class="item-top-center">
              <img src="../assets/img/home/lsjl.png" alt="">
            </div>
          </div>
          <div class="menu-item-bottom">历史记录</div>
        </div>
        <div class="menu-item menu-it" @click="navToMon(2)">
          <div class="menu-item-cover" v-if="access2==false"></div>
          <div class="menu-item-top cwgl-item-top">
            <div class="item-top-center">
              <img src="../assets/img/home/cwgl.png" alt="">
            </div>
          </div>
          <div class="menu-item-bottom">床位管理</div>
        </div>
      </div>
      <div class="home-main-it">
        <div class="menu-item menu-it" @click="navToMon(3)">
          <div class="menu-item-cover" v-if="access3==false"></div>
          <div class="menu-item-top sbgl-item-top">
            <div class="item-top-center">
              <img src="../assets/img/home/sbgl.png" alt="">
            </div>
          </div>
          <div class="menu-item-bottom">设备管理</div>
        </div>
        <div class="menu-item menu-it" @click="navToMon(5)">
          <div class="menu-item-cover" v-if="access3==false"></div>
          <div class="menu-item-top sbgl-item-top">
            <div class="item-top-center">
              <img src="../assets/img/home/router.png" alt="" class="item-top-center-img">
            </div>
          </div>
          <div class="menu-item-bottom">路由管理</div>
        </div>
        <div class="menu-item menu-it" @click="navToMon(4)">
          <div class="menu-item-cover" v-if="access5==false"></div>
          <div class="menu-item-top zhgl-item-top">
            <div class="item-top-center">
              <img src="../assets/img/home/zhgl.png" alt="">
            </div>
          </div>
          <div class="menu-item-bottom">账号管理</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import Vue from "vue";
import storage from 'good-storage'
var that= {};
export default {
  data(){
   return{
     userInfo:{},
     access0:true,
     access1:true,
     access2:true,
     access3:true,
     access4:true,
     access5:true,
   }
  },
  computed:{

  },
  beforeCreate(){
    that = this;
  },
  created(){

  },
  mounted:function(){
    that.getRem(1920, 100);
    that.userInfo = storage.get("userInfo");
    var arr = storage.get("userInfo").posts;
    if(arr.indexOf(0) < 0){
      that.access0 = false;
    }
    if(arr.indexOf(1) < 0){
      that.access1 = false;
    }
    if(arr.indexOf(2) < 0){
      that.access2 = false;
    }
    if(arr.indexOf(3) < 0){
      that.access3 = false;
    }
    if(arr.indexOf(4) < 0){
      that.access4 = false;
    }
    if(arr.indexOf(5) < 0){
      that.access5 = false;
    }
  },
  methods:{
    /*验权*/
    access(model){
      that.$get(
        that.global.url+'/manage/access',
          {
            managerId:storage.get("userInfo").managerId,
            model:model
          }
      ).then((response) => {
        console.log(response)
        if(response.code==200){
          if(model==0){
            that.$router.push({path: '/monitoring'});
          }else if(model==1){
            that.$router.push({path: '/history'});
          }else if(model==2){
            that.$router.push({path: '/bedManage'});
          }else if(model==3){
            that.$router.push({path: '/deviceManage'});
          }else if(model==4){
            that.$router.push({path: '/accountManage'});
          }else if(model==5){
            that.$router.push({path: '/routerManage'});
          }
        }else if(response.code==300){
          console.log('aa')
          that.error(response.message)
        }
        else{
          that.error('网络错误，请重试')
        }
      })
    },
    /*退出*/
    out(){
      console.log(storage.get('userInfo'))
      that.$delete(
        that.global.url+'/manage/login',
        {data:{
          token:storage.get('userInfo').token,
        }}

      ).then((response) => {
        if(response.code==200){
          storage.remove("userInfo");
          storage.remove("token");
          that.$router.push({path: '/'});
        }else if(response.code==300){
          that.error(response.message)
        }else{}
      })
    },
    navToMon(model){/*跳转监控平台*/
      console.log(model)
      that.access(model);
    },
    error (nodesc) {
        this.$Notice.error({
            title: '操作失败',
            desc: nodesc
      });
    }
  },
  components:{

  }
}

</script>
<style src="../assets/css/common.scss" lang="scss"></style>
<style src="../assets/css/home.scss" lang="scss"></style>
<style lang="scss" scoped>
   .item-top-center-img{
     width: 79px;
     height: 73px;
   }
  /* 路由图片大小 */  
</style>




