<template>
  <div class="all">
      <Topbar class="router">
        <img src="../assets/img/home/sbgl.png" alt="">
        <span>路由管理</span>
      </Topbar>
      <div class="device-choose clearfix">
        <span>科室选择：</span>
        <a href="javascript:void(0)" class="choose-btn" @click="active(index)" :class="activeDepart==index?'active':''" v-for="(item,index) in keshiList" :key="index">{{item.name}}</a>
        <em @click="addRouter">添加设备</em>
      </div>
      <div class="dev-main">
        <Table border :columns="columns" :data="routerList"></Table>
      </div>
      <Page :total="filter.total" :current="filter.current" class="pages"  @on-change="changePage" :page-size="sendTable.pageSize"/>
      <Toast :flow="model">
        <div class="qysb-box device-add-box">
          <div class="qysb-container">
            <img src="../assets/img/monitoring/guanbi.png" class="close guan" alt="" @click="quietRouter">
            <div class="device-title clearfix">
              <img src="../assets/img/device/router.png" alt="">
              <span>添加路由</span>
            </div>
            <div class="device-content">
              <form class="" autocomplete="false"  method="post">
                <div class="form-item clearfix">
                  <span class="sbbh form-name">设备名称：</span>
                  <Select v-model="routerMessage.routerid" style="width:2rem" class="select" @on-change="select1">
                      <Option v-for="(item,index) in routerMessage.routerName" :value="item.id" :key="index">{{ item.name }}</Option>
                  </Select>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">设备型号：</span>
                  <Select v-model="routerMessage.routerNumberId" style="width:2rem" class="select">
                      <Option v-for="(item,index) in routerMessage.routerNumList" :value="item.id" :key="index">{{ item.name }}</Option>
                  </Select>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">MAC地址：</span>
                  <Input class="input" v-model="routerMessage.macIp" placeholder="例：CC-1B-E0-E0-E8-F1" style="width: 2rem"/>
                  <img src="../assets/img/monitoring/router.png" alt="" class='router-img'>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">IP地址：：</span>
                  <Input class="input" v-model="routerMessage.IP" placeholder="例：192.168.30.120" style="width: 2rem"/>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">使用科室：</span>
                  <Select v-model="routerMessage.room" style="width:1rem" class="select" @on-change="select2">
                      <Option v-for="(item,index) in routerMessage.roomList" :value="item.departmentId" :key="index">{{ item.name }}</Option>
                  </Select>
                  <Select v-model="routerMessage.home" style="width:1rem;" class="select">
                      <Option v-for="(item,index) in routerMessage.homeList" :value="item.roomId" :key="index">{{ item.name }}</Option>
                  </Select>
                </div>
              </form>
            </div>
            <div class="ipt-group">
              <a href="javascript:void(0)" class="on"  @click="commit">确认</a>
              <a href="javascript:void(0)" class="off" @click="quietRouter">取消</a>
            </div>
          </div>
        </div>
      </Toast>
      <Toast :flow="model2">
        <div class="qysb-box device-add-box" style="height:4.9rem">
          <div class="qysb-container">
            <img src="../assets/img/monitoring/guanbi.png" class="close guan" alt="" @click="quietRouStop">
            <div class="device-title clearfix">
              <img src="../assets/img/device/router.png" alt="">
              <span>停用路由</span>
            </div>
            <div class="device-content">
              <div class="tip-content">
                <div class="tip-font">停用原因</div>
                <div class="tip-res">
                  <div class="tip-res-item" v-for="(item,index)  in tiplist" :key="index" :class="tipIndex==index?'activeTip':''" @click="activeTip(index)">{{item.name}}</div>
                </div>
              </div>
            </div>
            <div class="ipt-group">
              <a href="javascript:void(0)" class="on" @click="stopRouter">确认</a>
              <a href="javascript:void(0)" class="off" @click="quietRouStop">取消</a>
            </div>
          </div>
        </div>
      </Toast>
      <Toast :flow="model3">
        <div class="qysb-box device-add-box">
          <div class="qysb-container">
            <img src="../assets/img/monitoring/guanbi.png" class="close guan" alt="" @click="enableQuiet">
            <div class="device-title clearfix">
              <img src="../assets/img/device/router.png" alt="">
              <span>启用路由</span>
            </div>
            <div class="device-content">
              <form class="" autocomplete="false"  method="post">
                <div class="form-item clearfix">
                  <span class="sbbh form-name">设备名称：</span>
                  <div class="model3-item">{{enableRouter.routerName}}</div>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">设备型号：</span>
                  <div class="model3-item">{{enableRouter.routerNumber}}</div>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">MAC地址：</span>
                  <div class="model3-item">{{enableRouter.mac}}</div>
                  <img src="../assets/img/monitoring/router.png" alt="" class='router-img'>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">IP地址：：</span>
                  <Input class="input" v-model="enableRouter.ip" placeholder="例：192.168.30.120" style="width: 2rem"/>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">使用科室：</span>
                  <Select v-model="routerMessage.room" style="width:1rem" class="select" @on-change="select2">
                      <Option v-for="(item,index) in routerMessage.roomList" :value="item.departmentId" :key="index">{{ item.name }}</Option>
                  </Select>
                  <Select v-model="routerMessage.home" style="width:1rem" class="select">
                      <Option v-for="(item,index) in routerMessage.homeList" :value="item.roomId" :key="index">{{ item.name }}</Option>
                  </Select>
                </div>
              </form>
            </div>
            <div class="ipt-group">
              <a href="javascript:void(0)" class="on"  @click="enablecommit">确认</a>
              <a href="javascript:void(0)" class="off" @click="enableQuiet">取消</a>
            </div>
          </div>
        </div>
      </Toast>
  </div>
</template>

<script type="text/javascript">
import Vue from "vue";
var that= {};
import Topbar from '../components/topbar'
import Toast from '../components/toast'
import axios from 'axios';
import storage from 'good-storage'
import {Message} from 'iview'
export default {
  data(){
   return{
     tiplist:[
       {
         name:"故障，更换新设备"
       },
       {
         name:"故障维修"
       },
       {
         name:"其他"
       }
     ],//设备停用原因
     keshiList:[],//科室列表
     routerMessage:{
       routerName:[],//选择路由
       routerid:"",//路由id
       routerNumber:"",//路由型号
       routerNumberId:"",
       routerNumList:[],//型号列表
       roomList:[],
       homeList:[],
       room:"",
       home:"",
       macIp:"",
       IP:""
     },
     enableRouter:{
       routerName:"",
       routerNumber:"",
       mac:"",
       ip:"",
       id:"",
       departmentId:"",
       roomId:""
     },//启动路由发送字段
     activeDepart:0,//选择医院科室
     tipIndex:0,//选择提示
     sendTable:{
       Id:"",
       currentPage:1,
       pageSize:10
     },//获取信息列表所需字段
     sendMessage:{
       id:"",
       remark:""
     },//停止路由发送字段
     filter:{
       total:0,
       current:1
     },
     model:false,
     model2:false,
     model3:false,
     routerList:[
       {
         id:"1",
         repeaterName:"cassla蓝牙路由器",
         repeaterNum:"E100",
         mac:"CC:1B:E0:E0:E8:F1",
         ip:"192.168.31.190",
         departmentName:"普外科",
         roomName:"1089房",
         linkstatus:"1",
         remark:"暂无备注"
       }
     ],
     columns: [
          {
              title: '序号',
              align: 'center',
              key: 'id',
          },
          {
              title: '设备名称',
              align: 'center',
              key: 'repeaterName',
          },
          {
              title: '设备型号',
              align: 'center',
              key: 'repeaterNum',
          },
          {
              title: 'MAC地址',
              align: 'center',
              key: 'mac',
          },
          {
              title: 'IP',
              align: 'center',
              key: 'ip',
          },
          {
              title: '部署科室',
              align: 'center',
              key: 'departmentName',
          },
          {
              title: '安装位置',
              align: 'center',
              key: 'roomName',
          },
          {
              title: '状态',
              align: 'center',
              key: 'linkstatus',
              render:(h,params)=>{
                const row = params.row;
                const color = row.linkstatus == 0 ? '#0091FF' : row.linkstatus == 1 ? '#E02020' :row.linkstatus == 2 ? '#6D7278' : 'red';
                const text = row.linkstatus == 0 ? 'NO' : row.linkstatus == 1 ? 'OFF' : row.linkstatus == 2 ? '停用' : '错误';
                return h("div",{
                    style:{
                      color
                    }
                },text)
              }
          },
          {
              title: '备注',
              align: 'center',
              key: 'remark',
          },
          {
              title: '操作',
              align: 'center',
              key: 'operation',
              render:(h,params)=>{
                const row = params.row;
                const color = row.linkstatus  == '2'? '#0091FF' : '#6D7278';
                const text = row.linkstatus  == '2'? '启用' : '停用';
                var that = this
                return h("div",{
                    style:{
                      color,
                      cursor: "pointer"
                    },
                    on:{click:()=>{
                      console.log(row)
                      if(row.linkstatus  != '2'){
                        this.model2 = true;
                        this.sendMessage.id =  row.id
                      }else{
                        this.model3 = true;
                        that.enableRouter.id = row.id;
                        that.enableRouter.ip = row.ip;
                        that.enableRouter.mac = row.mac;
                        that.enableRouter.routerName = row.repeaterName;
                        that.enableRouter.routerNumber = row.repeaterNum;
                      }
                    }}
                },text)
              }
          },
      ],
   }
  },
  computed:{

  },
  beforeCreate(){
    that = this;
  },
  created(){

  },
  mounted:function(){
    var that = this;
    that.getRem(1920, 100);
    this.$get(that.global.url+'/hospital/departments',{}).then((res)=>{
      console.log(res)
      that.routerMessage.roomList = JSON.parse(JSON.stringify(res.data));
      res.data.unshift({
        departmentId:"",
        name:"全部"
      })
      that.keshiList = res.data
    })
    this.getData()
  },
  methods:{
    /*查询选中科室*/
    active(e){
      that.activeDepart = e;
      that.sendTable.Id = that.keshiList[e].departmentId
      that.sendTable.currentPage = 1
      console.log(that.sendTable.Id)
      this.getData()
      //查询操作...
    },
    activeTip(e){
      that.tipIndex = e
    },
    addRouter(){
      var that = this
      this.model = true;
      this.$get(that.global.url+'/repeater/machineType',{}).then((res)=>{
        console.log(res.data)
        that.routerMessage.routerName = res.data
        console.log(that.routerMessage.routerName)
      })
    },
    quietRouter(){
      this.model = false;
      that.routerMessage.routerName =[]
      that.routerMessage.routerid =""
      that.routerMessage.routerNumber =""
      that.routerMessage.routerNumList =[]
      that.routerMessage.homeList =[]
      that.routerMessage.room = ""
      that.routerMessage.home = ""
      that.routerMessage.macIp=""
      that.routerMessage.IP=""
      that.routerMessage.routerNumberId =""
    },//退出添加
    quietRouStop(){
      this.model2 = false;
      this.sendMessage.id = "";
      this.sendMessage.remark = "";
      this.tipIndex = 0
    },//退出停止路由
    enableQuiet(){
      this.model3 = false;
      that.routerMessage.homeList =[]
      that.routerMessage.home = ""
      that.routerMessage.room = ""
    },//关闭启动路由
    select1(){
      var that = this
      console.log(this.routerMessage.routerid)
      if(this.routerMessage.routerid){
        this.$get(that.global.url+'/repeater/machineNum',{
            id:this.routerMessage.routerid
          }).then((res)=>{
          console.log(res.data)
          that.routerMessage.routerNumList = res.data
        })
      }
    },
    select2(){
      var that = this;
      console.log(that.routerMessage.room)
      if(that.routerMessage.room){
        this.$get(that.global.url+'/hospital/rooms',{
            departmentId:that.routerMessage.room
          }).then((res)=>{
          console.log(res.data)
          that.routerMessage.homeList = res.data
        })
      }
    },
    commit(){
      var that = this;
      if(!that.routerMessage.routerid){
         this.error("设备名称不能为空")
      }else if(!that.routerMessage.routerNumberId){
        this.error("设备型号不能为空")
      }else if(!that.routerMessage.macIp){
        this.error("MAC地址不能为空")
      }else if(!/([A-Fa-f0-9]{2}-){5}[A-Fa-f0-9]{2}/.test(that.routerMessage.macIp)){
        this.error("MAC地址错误")
      }else if(!that.routerMessage.IP){
        this.error("IP地址不能为空")
      }else if(!/(\.((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})){3}/.test(that.routerMessage.IP)){
        this.error("IP地址不正确")
      }else if(!that.routerMessage.room){
        this.error("科室不能为空")
      }else if(!that.routerMessage.home){
        this.error("房间不能为空")
      }else{
        console.log(that.routerMessage)
        this.$post(that.global.url+'/repeater/repeater',{
          machineTypeId:that.routerMessage.routerNumberId,
          mac:that.routerMessage.macIp,
          departmentId:that.routerMessage.room,
          roomId:that.routerMessage.home,
          ip:that.routerMessage.IP
        }).then((res)=>{
          console.log(res)
          that.quietRouter();
          that.getData()
        })
      }
    },
    enablecommit(){
     var that = this;
     var id = that.enableRouter.id;
     var departmentId = that.routerMessage.room
     var roomId = that.routerMessage.home
     var ip = that.enableRouter.ip
     this.$put(that.global.url+'/repeater/start',{
       id,
       departmentId,
       roomId,
       ip
     }).then((res)=>{
       console.log(res)
       that.enableQuiet()
       that.getData()
     })
    },//启动路由提交
    error (msg) {
      this.$Notice.error({
        title:'添加失败',
        desc: msg
      })
    },
    stopRouter(){
      var that = this
      var id = that.sendMessage.id
      that.sendMessage.remark= that.tiplist[that.tipIndex].name
      var remark = that.sendMessage.remark
      console.log(that.sendMessage)
      this.$put(that.global.url+"/repeater/stop",{
          id,
          remark
      }).then((res)=>{
         console.log(res)
         that.quietRouStop()
         that.getData()
      })
    },//停用路由发送
    getData(){
      var that = this;
      this.$get(that.global.url+'/repeater/repeater',{
        departmentId:that.sendTable.Id,
        currentPage:that.sendTable.currentPage,
        pageSize:that.sendTable.pageSize
      }).then((res)=>{
        console.log(res)
        let data= res.data
        that.filter.total = data.totalCount
        that.routerList = data.list
      })
    },//获取数据
    changePage(e){
      this.sendTable.currentPage = e
      console.log(this.sendTable.currentPage)
      this.getData()
    }//分页触发事件
  },
  components:{
    "Topbar":Topbar,
    'Toast': Toast
  }
}

</script>
<style src="../assets/css/common.scss" lang="scss"></style>
<style src="../assets/css/router.scss" lang="scss"></style>
<style lang="scss" scoped>
  .guan{
    cursor: pointer;
  }
  .router-img{
    width: 1.98rem;
    height: 1.06rem;
    position: absolute;
    right: 1.9rem;
    cursor: pointer;
		transition: all 0.6s;
  }
  .router-img:hover{
     transform:scale(1.3);//设置缩放比例
            -ms-transform:scale(1.3);
            -webkit-transform:scale(1.3);
            -o-transform:scale(1.3);
            -moz-transform:scale(1.3);
  }
  .tip-content{
    display: flex;
    margin-left: 1.40rem;
  }
  .tip-font{
    font-family: PingFangSC-Regular;
    font-size: 0.16rem;
    color: #666666;
    text-align: right;
  }
  .tip-res{
     margin-left: 0.14rem;
  }
  .tip-res-item{
    width: 2.05rem;
    height: 0.35rem;
    background: #6D7278;
    border-radius: 0.02rem;
    border-radius: 0.02rem;
    text-align: center;
    line-height: 0.35rem;
    font-family: PingFangSC-Medium;
    font-size: 0.16rem;
    color: #FFFFFF;
    margin-bottom: 0.16rem;
    cursor: pointer;
  }
  .activeTip{
    background: #1890FF
  }
  .model3-item{
    display: inline-block;
    height: 0.32rem;
    line-height: 0.32rem;
    margin-right: 3.1rem
  }//模态框
  .select{
    float: left;
  }
</style>



