<template>
  <div class="zan-dialog" :class="flow ? 'zan-dialog--show' :'' " >
  <div class="zan-dialog__mask">
  <div class="zan-dialog__container">
    <slot></slot>
  </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'toast',
  data () {
    return {
      ishideModal: false, //模态框的状态  true-隐藏  false-显示
    }
  },
  props: {
     flow: {
       type:Boolean,
       // value:true,
       observer: function (news, olds, path) {
         console.log(news, olds)
       }
     },
   },
  methods:{
    hideModal:function(){
      that.ishideModal = true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.zan-dialog{
  /* display:none; */
}
.zan-dialog__mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.4);
  display:none;
}
.zan-dialog__container {
  /* position: fixed; */
  /* background:#fff; */
  top: 50%;
  /* transform: translateY(300%);/* 弹框弹出距离，与弹框布局高度有关，如300%表示弹起距离为3倍弹窗高度 */ */
  /* transition: all 0.4s ease; */
  z-index: 12;
}
.zan-dialog--show .zan-dialog__container {
   /* transform: translate(0,0); */
   width:100%;
}

.zan-dialog--show .zan-dialog__mask {
  display: block;
}
</style>



