import axios from 'axios';
import Qs from 'qs'
import storage from 'good-storage'
import router from "../router/index.js"
import {Notice} from 'iview'
import {Message} from 'iview'
const downloadUrl = url => {
  var ua = navigator.userAgent.toLowerCase(),s
  let iframe = document.createElement('iframe')
  if(s = ua.match(/firefox\/([\d.]+)/)){
    window.open(url)
  }else{
    iframe.src = url
    iframe.style.display = 'none'
  }
  iframe.onload = function () {
    document.body.removeChild(iframe)
  }
  document.body.appendChild(iframe)
}/*创建iframe进行下载*/
/* http request 拦截器 */
axios.interceptors.request.use(
  config => {
    if(storage.get('userInfo')){
      if(storage.get('userInfo').token){
        if(config.params){
          config.params.token = storage.get('userInfo').token;
        }else{
          config.data.token = storage.get('userInfo').token;
        }
      }
    }
     config.data = Qs.stringify(config.data);
     console.log(config.params)
    config.headers = {
      'Content-Type':'application/x-www-form-urlencoded'
    }

    return config;
  },
  error => {
    return Promise.reject(err);
  }
);
/*http response 拦截器*/
axios.interceptors.response.use(
  response => {
    console.log(response)
    if (response.headers && response.headers['content-type'] === 'application/octet-stream;charset=utf-8') {
      downloadUrl(response.request.responseURL)
      return {data:{rCode:'success'}}
    }/*判断是否下载*/
    if(response.data==''){
      setTimeout(function(){
        storage.remove("userInfo")
        router.push({path: '/'});
      }, 2000);
       Notice.error({
           title: '注意',
           desc: '您的账号已被别人登陆'
       });
    }else{

    }
    return response;
  },
  error => {
    return Promise.reject(error)
  }
)

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(url,{
      params:params
    })
    .then(response => {
      resolve(response.data);
    })
    .catch(err => {
      reject(err)
    })
  })
}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

 export function post(url,data){
   return new Promise((resolve,reject) => {
     axios.post(url,data)
          .then(response => {
            resolve(response.data);
          },err => {
            reject(err)
          })
   })
 }

 /**
* 封装put请求
* @param url
* @param data
* @returns {Promise}
*/

export function put(url,data){
 return new Promise((resolve,reject) => {
   axios.put(url,data)
        .then(response => {
          resolve(response.data);
        },err => {
          reject(err)
        })
 })
}

/**
* 封装delete请求
* @param url
* @param data
* @returns {Promise}
*/

export function deletes(url,data){
return new Promise((resolve,reject) => {
  axios.delete(url,data)
       .then(response => {
         resolve(response.data);
       },err => {
         reject(err)
       })
})
}



