<template id="home">
  <div class="all">
    <Topbar class="history">
      <img src="../assets/img/home/zhgl.png" alt="">
      <span>历史记录</span>
    </Topbar>
    <div class="history-choose clearfix">
      <div class="row row-flex">
        <div class="date">
          <span class="choose-title choose-title1">日期选择：</span>
          <DatePicker type="date" placeholder="选择日期" :options="options5" :value="dateChangeStart"  @on-change='dateChangeStartFn' style="width: 1.5rem"></DatePicker>
          <span class="choose-title choose-title2">至</span>
          <DatePicker type="date" :options="options4" :value="dateChangeEnd" placeholder="选择日期" @on-change='dateChangeEndFn' style="width: 1.5rem"></DatePicker>
        </div>
        <div class="keyword">
          <span class="keyword-title">病人姓名：</span>
          <input type="text" name="" value="" v-model="name" placeholder="请输入姓名检索">
        </div>
        <div class="search-btns clearfix">
          <a href="javascript:void(0)" class="search" @click='search()'>检索</a>
          <a href="javascript:void(0)" class="search" @click='extend()'>导出</a>
        </div>

      </div>
      <div class="row clearfix" v-if="userInfo.role!=2">
        <span class='choose-title'>科室选择：</span>
        <a href="javascript:void(0)" class="choose-btn" @click="active('-1')" :class="activeDepart==-1?'active':''">全部</a>
        <a href="javascript:void(0)" class="choose-btn" @click="active(item.departmentId)" :class="activeDepart==item.departmentId?'active':''" v-for="(item,index) in departmentList" :key="index">{{item.name}}</a>
      </div>
    </div>
    <div class="main">
      <Table border :columns="columns7" :data="historyList"></Table>
    </div>
    <Page :total="totalPage" :page-size='pageSize' :current='currentPage' @on-change="changePage" class="pages"/>
    <Toast :flow="historyStatus">
      <div class="box history-box">
        <div class="container">
          <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="history()">
          <div class="title clearfix">
            <img src="../assets/img/tw.png" alt="">
            <span>体温记录</span>
          </div>
          <div class="content history-content">

            <div class="zx-pic" id='myChart' style="width:11.45rem;height:4.8rem;"></div>
          </div>
          <div class="ipt-group">
            <a href="javascript:void(0)" class="on" @click='history()'>确认</a>
            <a href="javascript:void(0)" class="off" @click="history()">取消</a>
          </div>
        </div>
      </div>
    </Toast>
    <Toast :flow="extendStatus">
      <div class="box extend-box" style="width: 9.28rem;">
        <div class="container">
          <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="extend()">
          <div class="title clearfix">
            <img src="../assets/img/tw.png" alt="">
            <span>导出设置</span>
          </div>
          <div class="content extend-content" style="width: 7.72rem;">
            <form class="" autocomplete="false"  method="post">
              <div class="form-item clearfix">
                <span class="sbbh form-name">导出日期：</span>
                <DatePicker type="date" class='datepicker' placeholder="选择日期" :value="exTime.data"  @on-change='dateChangeExtendFn'></DatePicker>
              </div>
              <div class="form-item clearfix">
                <span class="sbbh form-name">选择时间点：</span>
                <div class="select-time">
                  <div v-for="(item,index) in exTime.time" :key="index" class="select-time-warp">
                    <TimePicker format="HH:mm" placeholder="选择时间" :value="exTime.time[index]" @on-change="timeChange(index,$event)"  style="width: 0.9rem"></TimePicker>
                    <Icon type="ios-close-circle" class="close-circle" @click="deleArr(index)"/>
                  </div>
                  <Icon type="ios-add-circle" class="add-circle" @click="addArr"/>
                </div>
                <!-- <Select class="select" name="" v-model="extendTime">
                  <Option :key="item.time" :value="item.time" v-for="item in arr">{{item.time}}</option>
                </Select> -->
              </div>
            </form>
          </div>
          <div class="ipt-group clearfix">
            <a href="javascript:void(0)" class="on" @click='extend()'>返回</a>
            <a href="javascript:void(0)" class="off" @click="extendFn()">导出</a>
          </div>
        </div>
      </div>
    </Toast>
  </div>
</template>

<script type="text/javascript">
import {Message} from 'iview'
import Vue from "vue";
var that= {};
import Topbar from '../components/topbar'
import Toast from '../components/toast'
import storage from 'good-storage'
export default {
  data(){
   return{
     exTime:{
       data:"",
       time:["",""]
     },//导出时间
     historyStatus:false,
     extendStatus:false,
     options4: {
         disabledDate (date) {
             return date && date.valueOf() < new Date(that.dateChangeStart) - 86400000;
         }
     },
     options5: {
         disabledDate (date) {
             return date && date.valueOf() > new Date(that.dateChangeEnd) - 86400000;
         }
     },
     animal:'男',
     arr:[
       {time:'15:00'},
       {time:'15:01'},
       {time:'15:02'}
     ],
     historyUseTimes:'',
     extendTime:'',
     activeDepart:'-1',
     pageSize:that.global.pageSize,
     currentPage:that.global.currentPage,
     totalPage:that.global.totalPage,
     departmentList:[],
     startDate:'',
     timesList:[],
     temperatureList:[],
     dateChangeStart:'',
     dateChangeEnd:'',
     dateChangeExtend:'',
     name:'',
     min:'',
     max:'',
     startTi:'',
     endTi:'',
     departmentId:'',
     userInfo:storage.get('userInfo'),
     historyList:[],
     startTime:'',
     endTime:'',
     columns7: [
         {
             title: '编号',
             align: 'center',
             key: 'recordId',
             width:100
         },
          {
              title: '姓名',
              align: 'center',
              key: 'patientName',
          },
          {
              title: '住院号',
              align: 'center',
              key: 'caseNum'
          },
          {
              title: '科室',
              align: 'center',
              key: 'departmentName'
          },
          {
              title: '病房-床位',
              align: 'center',
              key: 'location'
          },
          {
              title: '启用时间',
              align: 'center',
              key: 'startTime'
          },
          {
              title: '停止时间',
              align: 'center',
              key: 'endTime'
          },
          {
              title: '状态',
              align: 'center',
              key: 'usageState',
              render:(h,params)=>{
                if(params.row.usageState==0){
                  return h('span', {
                    "style":{
                      "color":"#0091FF"
                    },
                },'使用中')
                }else if(params.row.usageState==1){
                  return h('span', {
                    "style":{
                      "color":"rgba(0,0,0,0.50)"
                    }
                },'结束')
                }else{}
                return h('span',params.row.usageState)
            }
          },
          {
              title: '体温记录',
              key: 'action',
              width: 150,
              align: 'center',
              render: (h, params) => {
                  return h('div', [
                    h('img', {
                          domProps: {
                              src:require('../assets/img/tw.png')
                          },
                          style: {
                              color: "#0091FF"
                          },
                          on: {
                              click: () => {
                                  this.showHistory(params)
                              }
                          }
                      }),
                  ]);
              }
          }
      ],
      historyList: that.historyList
   }
  },
  computed:{

  },
  beforeCreate(){
    that = this;
  },
  created(){

  },
  mounted:function(){
    that.getRem(1920, 100);
    /*判断当前账号角色，如果是科室管理员只能看本科室的记录*/
    if(storage.get('userInfo').role==2){
      that.departmentId = that.userInfo.departmentId
    }else{

    }
    that.getNowTime();
    that.searchHistory();
    that.searchDepartment();
  },
  methods:{
    /*获取今天的日期*/
    getNowTime(){
      var date = new Date();
       var year = date.getFullYear();
       var month = date.getMonth() + 1;
       var strDate = date.getDate();
       that.dateChangeStart = year+'-'+month+'-'+strDate;
       that.dateChangeEnd = year+'-'+month+'-'+strDate;
    },
    /*选择分页的第几页*/
    changePage(page){
      console.log(page)
      that.currentPage = page;
      that.searchHistory();
    },
    /*关闭导出弹框*/
    extend(){
      that.extendStatus = !that.extendStatus;
      that.exTime.data=""
      that.exTime.time=[""]
    },
    /*导出*/
    extendFn(){
      if(this.exTime.data==""){
        that.error('请选择导出日期')
      }else if(!this.exTime.time.some((item)=>{if(item){return true}})||this.exTime.time.length ==0){
        that.error('请选择导出时间点')
      }else{
        that.extendStatus = !that.extendStatus;
        console.log(this.exTime.time)
        var timeList = ""
        for(var i=0;i<that.exTime.time.length;i++){
            if(that.exTime.time[i] !=""){
                timeList = timeList+","+that.exTime.data+" "+that.exTime.time[i]+":00"
            }
        }
        console.log(timeList.replace(/^,+/,"").replace(/,+$/,""))
        that.$get(that.global.url+'/patient/export',{
          timeList: timeList.replace(/^,+/,"").replace(/,+$/,"")
        }).then((res)=>{
          console.log(res)
          that.exTime.data=""
          that.exTime.time=[""]
        })
      }
    },
    /*检索*/
    search(){
      that.currentPage = 1;
      that.searchHistory();
    },
    /*查询历史记录*/
    searchHistory(){
      var data = {
        currentPage:that.currentPage,
        pageSize:that.pageSize,
      };
      if(that.dateChangeStart!=''){
        data['startDate'] = that.dateChangeStart;
      }
      if(that.dateChangeEnd!=''){
        data['endDate'] = that.dateChangeEnd;
      }
      if(that.name!=''){
        data['patientName'] = that.name;
      }
      if(that.departmentId!=''){
        data['departmentId'] = that.departmentId;
      }
      console.log(data)
      that.$get(
        that.global.url+'/patient/record',
        data
      ).then((response) => {
        console.log(response.data)
        if(response.code==200){
          for(var i=0;i<response.data.list.length;i++){
            if(response.data.list[i].startTime){
              response.data.list[i].startTime = response.data.list[i].startTime.substr(0,response.data.list[i].startTime.length-3);
            }
            if(response.data.list[i].endTime){
              response.data.list[i].endTime = response.data.list[i].endTime.substr(0,response.data.list[i].endTime.length-3);
            }
            response.data.list[i].location = response.data.list[i].roomName + response.data.list[i].bedName;
          }
          that.totalPage = Number(response.data.totalPage)*Number(that.pageSize);
          that.historyList = response.data.list;
        }else if(response.code==300){
          that.error(response.message)
        }else{}
      })
    },
    /*查询所有科室*/
    searchDepartment(){
      if(storage.get('userInfo').role==2){
        that.$get(
          that.global.url+'/hospital/department',
            {
              departmentId:storage.get('userInfo').departmentId,
            }
        ).then((response) => {
          if(response.code==200){
            that.departmentList = response.data;
            console.log(that.departmentList)
          }else if(response.code==300){
            that.error(response.message)
          }else{}
        })
      }else{
        that.$get(
          that.global.url+'/hospital/department',
            {
              departmentId:'',
            }
        ).then((response) => {
          if(response.code==200){
            that.departmentList = response.data;
            console.log(that.departmentList)
          }else if(response.code==300){
            that.error(response.message)
          }else{}
        })
      }
    },
    /*查询选中科室*/
    active(departmentId){
      that.activeDepart = departmentId;
      if(departmentId==-1){/*查询全部科室*/
        departmentId = '';
      }
      that.departmentId = departmentId;
    },
    /*弹出体温历史记录弹框*/
    showHistory(params){
      console.log(params.row.recordId)
      that.historyrecordId = params.row.recordId;
      that.getHistory();
    },
    /*查看体温记录*/
    getHistory(){
      that.$get(
        that.global.url+'/patient/temperature',
          {
            recordId:that.historyrecordId
          }
      ).then((response) => {
        console.log(response)
        if(response.code==200){
          if(response.data.length==0){

          }else{
            that.startTime = response.data.startTime.slice(5,response.data.startTime.length);
            that.endTime = response.data.endTime.slice(5,response.data.startTime.length);
            for(var i=0;i<response.data.list.length;i++){
              that.timesList.push(response.data.list[i].dateTime.slice(5,response.data.list[i].dateTime.length-3));
              that.temperatureList.push(response.data.list[i].temperature);
            }
            that.historyUseTimes = response.data.useTimes;
            console.log(that.timesList)
            console.log(that.temperatureList)
          }
          that.drawLine();
          that.history();
        }else if(response.code==300){
          that.error(response.message)
        }else{
          that.error('网络错误，请重试')
        }
      })
    },
    /*错误提示*/
    error (msg) {
      this.$Notice.error({
          title: '操作失败',
          desc: msg
      });
    },
    /*选择检索开始日期*/
    dateChangeStartFn(d){
      that.dateChangeStart = d;
    },
    /*选择检索结束日期*/
    dateChangeEndFn(d){
      that.dateChangeEnd = d;
    },
    /*选择导出日期*/
    dateChangeExtendFn(d){
      this.exTime.data = d;
      console.log(this.exTime)
    },
    /*选择导出时间*/
    timeChange(d,t){
      console.log(this.exTime)
       this.exTime.time[d] = t;
    },
    addArr(){
      if(this.exTime.time.length<10){
        this.exTime.time.push("")
      }else{
        this.$Notice.error({
          title: '操作失败',
          desc: "不能大于10个"
      });
      }
    },
    deleArr(e){
      this.exTime.time.splice(e,1)
       console.log(e)
    },
    /*绘制体温历史曲线*/
    drawLine(){
      let myChart = this.$echarts.init(document.getElementById('myChart'));
      myChart.clear();
      myChart.setOption({
         title: {
           text: '已使用'+this.historyUseTimes,
           textStyle:{
              color:'#282D32',
              fontStyle:'normal',
              fontWeight:'normal',
              fontFamily:' PingFangSC-Medium',
      　　　　 fontSize:20,
            },
            left:'45px',
            top:'26px'
          },
         tooltip: {
           trigger: 'axis',
         },
         xAxis: {
            type: 'category',
            boundaryGap: false,
            axisLine:{symbol:['none', 'arrow']},
            data: that.timesList,
            min: that.startTime,
             max: that.endTime,
             // interval: 30,
            axisLabel: {
               // interval:30,
               rotate:0,
            },
          },
          yAxis: {
              min: 34,
               max: 42,
               interval: 0.5,
               // data:that.temperatureList,
              axisLabel: {
              formatter: '{value}℃'
            },
              axisLine:{symbol:['none', 'arrow']},
          },
          dataZoom: [{
             start:0,
             end:50
         }, {
             type: 'inside',
             start:0,
             end:50,
             filterMode: 'empty'
         }, {
             start:0,
             end:50
         },],
          series: [{
            data: that.temperatureList,
            symbol: 'none',
            sampling: 'average',
            formatter: '{value}℃',
            type: 'line',
            smooth:true,
            areaStyle: {
              color: {
                type: 'linear',
                x: 1,
                y: 0,
                x2: 0,
                y2: 0,
                colorStops: [{
                    offset: 0, color: 'rgba(94,119,255,0.65)'
                }, {
                    offset: 1, color: 'rgba(94,119,255,1)'
                }],
                global: false
            }
          },
          lineStyle:{
            color:"rgba(94,119,255,1)",
            width:0
          },
          itemStyle:{
            normal:{
              borderWidth:0,
              color:'rgba(94,119,255,1)',
              borderColor:'rgba(94,119,255,1)',
            }

          },
          markLine:{
            itemStyle: {
              normal: {
                  borderWidth: 1,
                  lineStyle: {
                      type: 'dotted',
                      // color: '#FF9B9B',
                      width: 2,
                  },

                  label: {
                      formatter: '',
                      textStyle: {
                          fontSize: 16,
                          fontWeight: "bolder",
                      },
                  }
              },
            },
            data: [
              {
                  name: '高温',
                  lineStyle: {
                     color: '#FB924B'
                 },
                  yAxis: '37.5'
              },
              {
                  name: '低温',
                  lineStyle: {
                     color: '#007BB6'
                 },
                  yAxis: '36'
              },
            ]
          }
        }]
     });
    },
    /*弹出历史体温记录并将数据归零*/
    history(){
      that.historyStatus = !that.historyStatus;
      if(that.historyStatus==false){
        that.timesList = [];
        that.temperatureList = [];
      }
    }
  },
  components:{
    "Topbar":Topbar,
    'Toast': Toast
  }
}
</script>

<style src="../assets/css/common.scss" lang="scss"></style>
<style src="../assets/css/history.scss" lang="scss"></style>
<style lang="scss" scoped>
   .add-circle{
     font-size: 0.25rem;
     color: #0091FF ;
     margin-top: 0.07rem;
     float: left;
   }
   .select-time-warp{
     position: relative;
     float: left;
     margin-right: 0.2rem;
     margin-bottom: 0.1rem;
     cursor: pointer;
   }
   .select-time{
     float: left;
     margin-left: 0.1rem;
     width: 6rem
   }
   .close-circle{
     font-size: 0.2rem;
     color: #E02020 ;
     position: absolute;
     right: -0.1rem;
     top: -0.08rem;
     cursor: pointer;
   }
</style>



