<template id="home">
  <div class="all">
      <Topbar class="device">
        <img src="../assets/img/home/sbgl.png" alt="">
        <span>设备管理</span>

      </Topbar>
      <div class="device-choose clearfix">
        <span>查询选择：</span>
        <a href="javascript:void(0)" @click="active()" :class="status==-1?'active':''">不限</a>
        <a href="javascript:void(0)" @click="active(2)" :class="status==2?'active':''">正常状态</a>
        <a href="javascript:void(0)" @click="active(1)" :class="status==1?'active':''">停用状态</a>
        <!-- <em>导出</em> -->
        <em @click="add()">添加设备</em>
      </div>
      <div class="dev-main">
        <Table border :columns="columns7" :data="machineList"></Table>
      </div>
      <Page :total="totalPage" :page-size='pageSize' :current='currentPage' @on-change="changePage" class="pages"/>
      <Toast :flow="addStatus">
        <div class="qysb-box device-add-box">
          <div class="qysb-container">
            <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="add()">
            <div class="device-title clearfix">
              <img src="../assets/img/device/icon.png" alt="">
              <span>添加设备</span>
            </div>
            <div class="device-content">
              <form class="" autocomplete="false"  method="post">
                <div class="form-item clearfix">
                  <span class="sbbh form-name">设备名称：</span>
                  <Input class="input" v-model="addName"/>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">设备型号：</span>
                  <Input class="input" v-model="addModel"/>
                </div>
                <div class="form-item clearfix">
                  <span class="form-name">设备编号：</span>
                  <Input class='textarea' :rows="5" :autosize="{maxRows:5,minRows: 5}" v-model="addNum" type="textarea"/>
                  <img src="../assets/img/monitoring/case.png" alt="" class='case-img'>

                </div>
                <div class="form-item clearfix">
                  <span class="form-name">使用科室：</span>
                  <Select class="select" name="" v-model="department">
                    <Option :key="item.departmentId" :value="item.departmentId" v-for="item in departmentList" >{{item.name}}</option>
                  </Select>
                </div>
                <div class="form-item clearfix">
                  <p class='tips-form'>注：批量导入时编号用英文符号“;”隔开，导入时将“B53.00122608”中的.改为/</p>
                </div>
              </form>
            </div>
            <div class="ipt-group clearfix">
              <a href="javascript:void(0)" class="on" @click="addMachine()">确认</a>
              <a href="javascript:void(0)" class="off" @click="add()">取消</a>
            </div>
          </div>
        </div>
      </Toast>
      <Toast :flow="stopStatus">
        <div class="qysb-box device-stop-box">
          <div class="qysb-container">
            <img src="../assets/img/monitoring/guanbi.png" class="close" alt="" @click="toStop()">
            <div class="device-title clearfix">
              <img src="../assets/img/device/icon.png" alt="">
              <span>设备停用</span>
            </div>
            <div class="content stop-content">
              <form class="" action="index.html" method="post">
                <div class="form-item clearfix">
                  <span class="form-name">停用原因：</span>
                  <Input class='textarea' :rows="8" v-model="tyyy" :autosize="{maxRows:8,minRows: 8}" type="textarea" placeholder="请输入设备停用原因(必填)"/>
                  <p>注：如丢失，故障等</p>
                </div>
              </form>
            </div>
            <div class="ipt-group clearfix">
              <a href="javascript:void(0)" class="on" @click="stopMachine()">确认</a>
              <a href="javascript:void(0)" class="off" @click="stop()">取消</a>
            </div>
          </div>
        </div>
      </Toast>
  </div>
</template>

<script type="text/javascript">
import Vue from "vue";
var that= {};
import Topbar from '../components/topbar'
import Toast from '../components/toast'
import axios from 'axios';
import storage from 'good-storage'
import {Message} from 'iview'
export default {
  data(){
   return{
     addStatus:false,
     stopStatus:false,
     status:-1,
     addName:'',
     department:'',
     addModel:'',
     switchactiveIndex:'',
     addNum:'',
     pageSize:that.global.pageSize,
     currentPage:that.global.currentPage,
     totalPage:that.global.totalPage,
     tyyy:'',
     machineList:[],
     departmentList:[],
     columns7: [
          {
              title: '设备名称',
              align: 'center',
              key: 'name',
          },
          {
              title: '设备型号',
              align: 'center',
              key: 'machineModel'
          },
          {
              title: '设备编码',
              align: 'center',
              key: 'machineNum'
          },
          {
              title: '使用科室',
              align: 'center',
              key: 'departmentName'
          },
          {
              title: '状态',
              align: 'center',
              key: 'usageState',
              render:(h,params)=>{
                if(params.row.usageState==0){
                  return h('span', {
                    "style":{
                      "color":"#0091FF"
                    },
                },'正常')
                }else if(params.row.usageState==1){
                  return h('span', {
                    "style":{
                      "color":"rgba(0,0,0,0.50)"
                    }
                },'停用')
              }else if(params.row.usageState==2){
                  return h('span', {
                    "style":{
                      "color":"#0091FF"
                    }
                },'正常')
                }else{}
                return h('span',params.row.usageState)
            }
          },
          {
              title: '添加日期',
              align: 'center',
              key: 'createTime'
          },
          {
              title: '备注',
              align: 'center',
              key: 'remark'
          },
          {
              title: '操作',
              key: 'usageState',
              width: 150,
              align: 'center',
              render: (h, params) => {
                  return h('div',{
                    style: {
                        position: 'relative'
                    },
                  },
                  [
                    h('i-switch', {
                          props: {
                            type: 'primary',
                            value: params.row.usageState==0 || params.row.usageState==2,
                            'true-value':true,
                            'falseValue':false,
                          },
                          style: {
                              marginRight: '5px'
                          },
                          on: {

                          }
                      },
                      [
                        h('span', {
                        slot: "open",
                        domProps: {
                            innerHTML: '开'
                        }
                    }),
                    h('span', {
                        slot: "close",
                        domProps: {
                            innerHTML: '关'
                        }
                    }),
                    ]
                  ),
                  h('div', {
                        props: {
                          type: 'primary',
                          value: params.row.usageState==0,
                          'true-value':true,
                          'falseValue':false,
                        },
                        style: {
                            position:'absolute',
                            zIndex:'100000',
                            width:'44px',
                            height:'22px',
                            top:0,
                            left:'33px'
                        },
                        on: {
                            'click':()=> {
                              this.switch(params)
                            }
                        }
                    }),
                  ]
                );
              }
          }
      ],
   }
  },
  computed:{

  },
  beforeCreate(){
    that = this;
  },
  created(){

  },
  mounted:function(){
    that.getRem(1920, 100);
    that.searchMachine();
    that.searchDepartment();
    console.log(that.status)
  },
  methods:{
    active(status){
      console.log(status)
      if(status==undefined){
        that.status = -1;
      }else{
        that.status = status;
      }
      console.log(that.status)
      that.currentPage = 1;
      that.searchMachine(status);
    },
    toStop(){
      that.stop();
    },
    /*查询所有科室*/
    searchDepartment(){
      that.$get(
        that.global.url+'/hospital/department',
          {
            departmentId:'',
          }
      ).then((response) => {
        if(response.code==200){
          that.departmentList = response.data;
          console.log(that.departmentList)
        }else if(response.code==300){
          that.error(response.message)
        }else{}
      })
    },
    changePage(page){
      console.log(page)
      that.currentPage = page;
      if(that.status==-1){
        that.searchMachine('');
      }else{
        that.searchMachine(that.status);
      }

    },
    /*查询所有设备*/
    searchMachine(status){
      if(status==-1){
        status = '';
      }
      that.$get(
        that.global.url+'/machine/machine',
          {
            usageState:status,
            currentPage:that.currentPage,
            pageSize:that.pageSize
          }
      ).then((response) => {
        if(response.code==200){
          that.machineList = response.data.list;
          that.totalPage = Number(response.data.totalPage)*Number(that.pageSize);
          console.log(that.pageSize)
          console.log(response.data.totalPage)
          console.log(that.totalPage)
          console.log(that.machineList)
        }else if(response.code==300){
          that.error(response.message)
          that.machineList = [];
          that.totalPage = 0;
        }else{
          that.machineList = [];
          that.totalPage = 0;
        }
      })
    },
    /*成功提示*/
    success (msg) {
      this.$Notice.success({
          title: '操作成功',
          desc: msg
      });
    },
    /*错误提示*/
    error (msg) {
      this.$Notice.error({
        title: '操作失败',
        desc: msg
      })
    },
    /*添加设备*/
    addMachine(){
      var arr = that.addNum.split(';')
      if(that.addName==''){
        that.error('请填写设备名称')
      }else if(that.addModel==''){
        that.error('请填写设备型号')
      }else if(that.addNum==''){
        that.error('请填写设备编号')
      }else if(that.department==''){
        that.error('请选择科室')
      }else{
        that.$post(
          that.global.url+'/machine/machine',
            {
              name:that.addName,
              machineModel:that.addModel,
              machineNums:arr,
              departmentId:that.department
            }
        ).then((response) => {
          if(response.code==200){
            that.success();
            console.log(response)
            if(that.status==-1){
              that.searchMachine();
            }else{
              that.searchMachine(that.status);
            }
            that.add();
          }else if(response.code==300){
            that.error(response.message)
          }else{}
        })
      }
    },
    /*停用设备*/
    stopMachine(){
      console.log(that.machineId)
      console.log(that.status)

      if(that.tyyy==''){
        that.error('请填写停用原因')
      }else{
        that.$delete(
          that.global.url+'/machine/machine',
          {data:{
            machineId:that.machineId,
            remark:that.tyyy,
          }}
        ).then((response) => {
          if(response.code==200){
            that.success();
            console.log(response)
            if(that.status==-1){
              that.searchMachine();
            }else{
              that.searchMachine(that.status);
            }
            that.stop();
          }else if(response.code==300){
            that.error(response.message)
          }else{}
        })
      }
    },
    add(){
      that.addStatus = !that.addStatus;
      if(that.addStatus==false){
        that.addName = '';
        that.addModel = '';
        that.addNum = '';
        that.department = '';
      }
    },
    stop(){
      that.stopStatus = !that.stopStatus;
    },
    switch(params){
      that.switchactiveIndex = params.index;
      console.log(that.switchactiveIndex)
      console.log(that.machineList[that.switchactiveIndex].usageState)
      that.machineId = that.machineList[that.switchactiveIndex].machineId;
      console.log(that.machineId)
      if(that.machineList[that.switchactiveIndex].usageState==0){
      that.stop();
    }else if(that.machineList[that.switchactiveIndex].usageState==2){
      that.error('设备正在使用中')
    }
    else{
      that.error('暂不提供启用服务')
    }
    },
  },
  components:{
    "Topbar":Topbar,
    'Toast': Toast
  }
}

</script>
<style src="../assets/css/common.scss" lang="scss"></style>
<style src="../assets/css/device.scss" lang="scss"></style>



